import { Modal } from './modal';

export function filmTrailerModalWindow() {
  const filmTrailerModal = new Modal(
    '.trailers-btns-list',
    '.trailer-modal__close-btn',
    '.trailer-modal-overlay',
    '.trailer-modal'
  );

  filmTrailerModal.openBtn.addEventListener(
    'click',
    filmTrailerModal.openTrailerModal.bind(filmTrailerModal)
  );
}
